.card {
  display: flex;
  width: 50%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 0.8rem;
  padding: 1.4rem 2rem;
  margin: 2rem;
  background-color: beige;
}

h2 {
  margin: 0 0 0.8rem 0;
  font-size: 1.4rem;
  font-family: emoji;
  color: #eb1137;
}

.input-body {
  margin-bottom: 1rem;
}

label {
  float: left;
  margin-right: 0.8rem;
  color: #eb1137;
  font-size: 1rem;
  font-weight: 500;
}

input[type="text"],
input[type="email"] {
  font-size: 1rem;
  padding: 0.3rem;
  float: right;
  clear: both;
}

.ml {
  margin-left: 5rem;
}

button {
  color: #eb1137;
  background-color: rgba(249, 106, 29, 0.4);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  border: none;
  font-weight: 500;
  font-size: 1rem;
  opacity: 0.9;
  transform: scale(0.95);
  box-shadow: 0px 0px 0.15rem #ffbc048e;

  transition: 0.15s;
}

button:hover {
  box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.25);

  opacity: 1;
  transform: scale(1);
}
